<template>
  <canvas
    class="ec-canvas"
    :id="canvasId"
    :canvasId="canvasId"
    @touchstart="touchStart"
    @touchmove="touchMove"
    @touchend="touchEnd"
    @error="error"
  ></canvas>
</template>

<script>
import WxCanvas from './wx-canvas';
import * as echarts from './echarts.min';

let ctx;

export default {
  props: {
    canvasId: {
      type: String,
      default: 'ec-canvas'
    },
    ec: {
      type: Object,
      default: () => ({})
    },
    forceUseOldCanvas: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isUseNewCanvas: false
    };
  },
  ready() {
    this.init();
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      const { canvasId } = this;
      this.isUseNewCanvas = !this.forceUseOldCanvas;
      
      if (this.isUseNewCanvas) {
        // 使用新的canvas
        this.initByNewWay(canvasId);
      } else {
        // 使用旧的canvas
        this.initByOldWay(canvasId);
      }
    },
    
    initByOldWay(canvasId) {
      // 获取canvas上下文
      ctx = uni.createCanvasContext(canvasId, this);
      
      const canvas = new WxCanvas(ctx, canvasId);
      
      echarts.setCanvasCreator(() => canvas);
      
      const query = uni.createSelectorQuery().in(this);
      query.select(`#${canvasId}`)
        .boundingClientRect(res => {
          if (!res) {
            setTimeout(() => this.initByOldWay(canvasId), 50);
            return;
          }
          
          const width = res.width;
          const height = res.height;
          
          // 初始化echarts实例
          const chartInstance = echarts.init(canvas, null, {
            width,
            height,
            devicePixelRatio: uni.getSystemInfoSync().pixelRatio
          });
          
          // 设置绘图函数
          canvas.setChartInstance(chartInstance);
          
          // 传递实例给父组件
          this.$emit('inited', chartInstance);
          
          // 设置图表配置
          if (this.ec && this.ec.option) {
            chartInstance.setOption(this.ec.option);
          }
        })
        .exec();
    },
    
    initByNewWay(canvasId) {
      const query = uni.createSelectorQuery().in(this);
      query.select(`#${canvasId}`)
        .fields({ node: true, size: true })
        .exec(res => {
          if (!res[0] || !res[0].node) {
            // 如果没有找到节点，回退到旧的方式
            this.isUseNewCanvas = false;
            this.initByOldWay(canvasId);
            return;
          }
          
          const width = res[0].width;
          const height = res[0].height;
          
          const canvas = res[0].node;
          const ctx = canvas.getContext('2d');
          
          const chartInstance = echarts.init(canvas, null, {
            width,
            height,
            devicePixelRatio: uni.getSystemInfoSync().pixelRatio,
            context: ctx
          });
          
          // 传递实例给父组件
          this.$emit('inited', chartInstance);
          
          // 设置图表配置
          if (this.ec && this.ec.option) {
            chartInstance.setOption(this.ec.option);
          }
        });
    },
    
    touchStart(e) {
      if (this.ec && this.ec.disableTouch) {
        return;
      }
      const chartInstance = this.getChartInstance();
      if (chartInstance && e.touches.length > 0) {
        const touch = e.touches[0];
        const handler = chartInstance.getZr().handler;
        handler.dispatch('mousedown', {
          zrX: touch.x,
          zrY: touch.y
        });
        handler.dispatch('mousemove', {
          zrX: touch.x,
          zrY: touch.y
        });
        handler.processGesture(e, 'start');
      }
    },
    
    touchMove(e) {
      if (this.ec && this.ec.disableTouch) {
        return;
      }
      const chartInstance = this.getChartInstance();
      if (chartInstance && e.touches.length > 0) {
        const touch = e.touches[0];
        const handler = chartInstance.getZr().handler;
        handler.dispatch('mousemove', {
          zrX: touch.x,
          zrY: touch.y
        });
        handler.processGesture(e, 'change');
      }
    },
    
    touchEnd(e) {
      if (this.ec && this.ec.disableTouch) {
        return;
      }
      const chartInstance = this.getChartInstance();
      if (chartInstance) {
        const handler = chartInstance.getZr().handler;
        handler.dispatch('mouseup', {});
        handler.dispatch('click', {});
        handler.processGesture(e, 'end');
      }
    },
    
    error(e) {
      console.error('ECharts错误:', e);
    },
    
    getChartInstance() {
      return this.ec && this.ec.instance;
    }
  }
};
</script>

<style>
.ec-canvas {
  width: 100%;
  height: 100%;
}
</style> 